*{
  font-family: Helvetica, Arial, sans-serif;
}
body{
  padding: 60px;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
  border-color: blue;
  border-radius: 8px;
  /* border-radius: 50%; */
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6%;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  background-color: rgb(0, 153, 255);

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}